
@tailwind base;
@tailwind components;
@tailwind utilities;



input[type="text"],input[type="email"],input[type="password"],input[type="number"],textarea {
  @apply w-full border my-2 py-2 px-3 rounded-2xl;
}

button.primary{
  background-color: #F5385D;
  @apply p-2 w-full text-white rounded-2xl;
}